<template>
	<section class="portfolio-works">
		 <div class="portfolio-filter">
			<ul class="portfolio-filter__list">
			  <li class="portfolio-filter__item" :class='{active: activeAll}' @click='filterWorks()'>Всі</li>
			  <!-- /.portfolio-filter__item -->
			  <li class="portfolio-filter__item" :class='{active: activeSites}' @click='filterWorks("sites")'>Сайти</li>
			  <!-- /.portfolio-filter__item -->
			  <li class="portfolio-filter__item" :class='{active: activePersonal}' @click='filterWorks("personal")'>Особисті проекти</li>
			  <!-- /.portfolio-filter__item -->
			</ul>
			<!-- /.portfilio-filter__list -->
		 </div>
		  <!-- /.portfolio-filter -->
	  	<div class="row">
			<div class="col-12 col-lg-4" v-for='work in filtered' :key='work.id'>
				<div class="flip portfolio-item">
					<div class="edit" v-if='User'>
						<router-link :to="{ name: 'edit_work', params: { title: $_friendlyUrl(work.title), id: work.id }}">
 				  			<img src="../blog/icons/edit-regular.svg" alt="Edit">
						</router-link>
						
					</div>
				    <div class="front" :style="{'background-image': `url(${work.img})`}">
				       <h5 class="blog-work__title">{{work.title}}</h5>
				    </div>
				    <div class="back" :style="{'background-image': `url(${work.img})`}">
				    	<div class="portfolio-item__links">
							<router-link title='Дивитись деталі' class="portfolio-item__link" tag='span' :to="{ name: 'single-work', params: { title: $_friendlyUrl(work.title),id: work.id }}">
							  Дивитись деталі
							</router-link>
							<a title="Переглянути проект" :href="work.url" class="portfolio-item__link">
							 Переглянути проект
							</a>
						</div>
				    </div>
				</div>
			</div>
			<!-- /.col-12 col-lg-4 -->
		</div>
		<!-- /.row -->

			
		  <div class="none" v-if='filtered.length === 0'>Робіт не знайдено!</div>
	</section>
	<!-- /.portfolio-works -->
	 
</template>
<script>
	import { db } from '@/main';

	export default {
		name: 'portfolioWorks',
		created() {
			this.filtered = this.works;
		},
	  	data () {
			return {
				works: [],
				filtered: [],
				activeSites: false,
				activePersonal: false,
				activeAll: true
			}
	 	},
	  	firestore () {
		 	return {
		   		works: db.collection('works').orderBy('createdAt')
		 	}
	  	},
	  	methods: {
			filterWorks(cat) {
		  		this.filtered = [];
		  		if(cat) {
					this.clearActive();

					this.filtered = this.works.filter(item => item.cat === cat);

					cat == 'sites' ? this.activeSites = true : this.activePersonal = true;
		  		} else {
					this.clearActive();
					this.filtered = this.works;
					this.activeAll = true;
		  		}
			},
			clearActive() {
		  		this.activeAll = this.activeSites = this.activePersonal = false;
			},
	  	},
  	   	computed: {
  			User() {
  				return this.$store.getters.User;
  			}
  		},
	}
</script>
<style lang="sass" scoped>
	@import '../../assets/sass/_vars'

	.flip
		position: relative
		margin-right: 2px
		margin-bottom: 1em
		width: 100%
		.front, .back
			display: block
			transition-timing-function: cubic-bezier(.175, .885, .32, 1.275)
			transition-duration: .5s
			transition-property: transform, opacity
			color: white
			width: inherit
			background-size: cover!important
			background-position: center!important
			height: 250px
			padding: 1em 2em
			background: $accent
			border-radius: 10px
			overflow-y: hidden

		.front
			transform: rotateY(0deg)
			position: relative
			&::before
				content: ''
				position: absolute
				top: 0
				left: 0
				width: 100%
				height: 100%
				background-color: rgba(0,0,0, .6)
				z-index: -1
		.back
			position: absolute
			opacity: 0
			top: 0px
			left: 0px
			width: 100%
			height: 100%
			transform: rotateY(-180deg)
			border: 2px solid $accent
			&::before
				content: ''
				position: absolute
				top: 0
				left: 0
				width: 100%
				height: 100%
				background-color: rgba($accent, .8)
				z-index: -1
		&:hover
			.front
				transform: rotateY(180deg)
			.back
				opacity: 1
				transform: rotateY(0deg)
</style>